html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video { margin: 0; padding: 0; border: 0; font-size: 100%; font: inherit; vertical-align: baseline; text-decoration:none;}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section { display: block; }

body { line-height: 1; overflow: auto}

ol,
ul { list-style: none; }

blockquote,
q { quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after { content: none; }

table { border-collapse: collapse; border-spacing: 0; }

button::-moz-focus-inner {
    border:0;
    padding:0;
}


* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

h1 {
    font-size: 3.5em;
    line-height: 1em;
    margin-bottom: 0.2em;
}

h2 {
    font-size: 1.5em;
    font-weight: 200;
}

h3 {
    font-size: 0.9em;
    margin-bottom: 0.5em;
    font-weight: bold;
}

a {
    color: #333;
    display: inline-block;
}

b{
    font-weight: bold;
}

input, textarea{
    font-family: inherit;
}


