@import './reset.css';

.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
  min-height: 100vh;
  font-family: 'Titillium Web', sans-serif;
}

h1{
  font-weight: 100;
  font-size: 2em;
  margin-bottom: 10px;
}

.left-pane {
  width: 1em;
}

.right-pane {
  -ms-flex-positive: 1;
      flex-grow: 1;
  background-image: -webkit-linear-gradient(316deg,  #000, #03032b);
  background-image: -o-linear-gradient(316deg,  #000, #03032b);
  background-image: linear-gradient(134deg,  #000, #03032b);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  overflow: hidden;
}

.right-pane .slots{
  
}

.slot{
  display: -ms-flexbox;
  display: flex;
}

.slot-car{
  height: 11em;
  width: 22em;
  border: solid 0px #fff;
  border-top-width: 7px;
  border-right-width: 7px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.slot:last-child .slot-car:after{
  content: '';
  width: 100%;
  left: 0;
  right: 0;
  position: absolute;
  top: 100%;
  border: solid 0px #fff;
  border-bottom-width: 7px;
  border-left-width: 7px;
}

.slot-car .occupied{
  position: absolute;
  right: 7px;
  top: 10px;
  bottom: 10px;
  -webkit-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
  -webkit-transform: translateX(-150%);
      -ms-transform: translateX(-150%);
          transform: translateX(-150%);
  opacity: 0;
  z-index: 1;
}

.slot-car .occupied.active{
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
}

.slot-car .occupied:after{
  content: '';
  position: absolute;
  height: 200px;
  width: 101%;
  background: -webkit-gradient(linear, left top, left bottom, from(#000), to(rgba(0,0,0,0)));
  background: -webkit-linear-gradient(#000, rgba(0,0,0,0));
  background: -o-linear-gradient(#000, rgba(0,0,0,0));
  background: linear-gradient(#000, rgba(0,0,0,0));
  left: 0;
  top: 40%;
  z-index: 1;
  -webkit-transform-origin: top center;
      -ms-transform-origin: top center;
          transform-origin: top center;
  -webkit-transform: skew(-21deg) skewY(5deg);
      -ms-transform: skew(-21deg) skewY(5deg);
          transform: skew(-21deg) skewY(5deg);
}

.slot-car .occupied img{
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 2;
}

.slot-description{
  background-color: transparent;
  padding: 1em;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  text-align: left;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
}

.pref{
  position: relative;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
}

.pref img{
  width: 55px;
}

.slot-label{
  font-weight: 600;
  letter-spacing: 1px;
  font-size: 1.2em;
  background-color: #fff;
  color: #000;
  padding: 0.5em 1em;
  margin-bottom: 10px;
  -webkit-animation: 1s reveal;
          animation: 1s reveal;
}

.slot-label.irregular{
  color: #fff;
  background-color: #f00;
}

.slot-label.occupied{
  color: #000 ;
  background-color: #ffea00;
}

.slot-label.free{
  color: #fff;
  background-color: #168400;
}

@-webkit-keyframes reveal{
  from{
    max-width: 0px;
    opacity: 0;
    -webkit-transform: translateX(-40px);
            transform: translateX(-40px)
  }
  to{
    max-width: 300px;
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px)
  }
}

@keyframes reveal{
  from{
    max-width: 0px;
    opacity: 0;
    -webkit-transform: translateX(-40px);
            transform: translateX(-40px)
  }
  to{
    max-width: 300px;
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px)
  }
}


